<template>
  <div>
    <b-card no-body class="">
      <div class="">
        <b-row class="p-1">
          <!-- v-if="roles.isAdmin" -->
          <div v-if="roles.isAdmin" class="d-flex flex-column ml-1">
            <!-- {{ companyList }} -->
            <!-- {{ selectedCompany }} -->
            <label class="saj-text">{{ $t("Company") }}:</label>
            <b-form-select
              v-model="selectedCompany"
              :options="companyList"
              size="sm"
              class="saj-text m-0"
              style="width: 190px; margin-left: 14px; height: 34px"
            />
          </div>
          <div class="d-flex flex-column ml-1">
            <label class="saj-text" style="margin-left: 1px"
              >{{ $t("Cycle") }}:</label
            >
            <!-- <div class="bg-danger"> -->
            <Datepicker
              id="input-id"
              v-model="selectedCycle"
              :value="customFormatter"
              :format="customFormatter"
              minimum-view="year"
              name="datepicker"
              input-class="year-custom"
              class="show-only-year"
            />
            <!-- </div> -->
          </div>
          <div
            v-if="selectedCompany !== 0 || !roles.isAdmin"
            class="d-flex flex-column ml-1"
          >
            <label class="saj-text">{{ $t("Filter by") }}:</label>
            <div class="row">
              <b-form-select
                v-model="filterBy"
                :options="
                  roles.isHOD
                    ? listFilterBy.filter((x) => x.value !== 'Department')
                    : listFilterBy
                "
                size="sm"
                class="saj-text"
                style="width: 190px; margin-left: 14px; height: 34px"
              />
            </div>
          </div>
        </b-row>
        <b-row class="p-1 d-flex justify-content-end align-items-end">
          <!-- overall <p>{{ isOverall }}</p>
          company <p>{{ selectedCompany }}</p>
          filterby <p>{{ filterBy }}</p> -->

          <b-button
            md="4"
            variant="primary"
            class="saj-button mr-1"
            block
            style="width: 120px; height: 45px"
            @click="
              filter(),
                (graphData = []),
                (refreshGraph += 1),
                (show = false),
                filterBy !== null ? (isOverall = false) : (isOverall = true),
                (search = true)
            "
          >
            <!-- <feather-icon icon="SearchIcon" /> -->
            <span class="" style=""> {{ $t("Filter") }} </span>
          </b-button>
          <b-button
            md="4"
            variant="primary"
            class="mr-1 saj-button"
            block
            style="width: 120px; height: 45px"
            :disabled="filterBy === null && selectedCompany !== 0"
            @click="clearButton()"
          >
            <span class="" style=""> {{ $t("Clear") }} </span>
          </b-button>
        </b-row>
      </div>
      <!-- {{ graphData }} -->
    </b-card>
    <b-overlay :show="search" rounded="sm" class="mt-3">
      <template #overlay>
        <div
          class="d-flex flex-column align-items-center justify-content-center"
          style="margin-top: 30vh"
        >
          <b-spinner variant="primary" label="Spinning" />
          <p class="mt-1">{{ $t("Fetching Data") }}...</p>
        </div>
      </template>
      <div v-if="show">
        <div :key="refreshGraph" class="">
          <b-tabs content-class="mt-3">
            <b-tab :title="$t('Bar Graph')" active>
              <bar-graph :graph="graphValue" :category-type="catType" />
            </b-tab>
            <b-tab :title="$t('Line Graph')">
              <line-graph :graph="graphValue" :category-type="catType" />
            </b-tab>
            <b-tab
              v-if="catType !== 'performance'"
              :title="$t('Overall Graph')"
            >
              <overall-graph :graph="graphValue" :category-type="catType" />
            </b-tab>
          </b-tabs>
        </div>
      </div>

      <b-card v-if="show">
        <!-- summary table -->
        <span class="saj-title">{{ $t("Summary Table") }}</span>
        <b-tabs content-class="mt-1">
          <b-tab :title="$t('Performance Grade')" active>
            <div class="container">
              <div class="d-flex justify-content-end">
                <b-form-input
                  v-model="searchSummary"
                  style="width: 250px"
                  :placeholder="$t('Search') + '...'"
                  size="sm"
                  class="d-flex align-items-end"
                />
              </div>

              <b-table
                sticky-header="500px"
                responsive
                :items="summaryList"
                :fields="summaryFields"
                :filter="searchSummary"
                bordered
                show-empty
                style=""
                class="mt-1 mb-1 saj-subtitle"
              >
                <!-- <template #head()="data">
                <span
                  class="saj-text d-flex justify-content-center"
                >{{ $t(data.label) }}</span>
              </template> -->
                <template #emptyfiltered="">
                  <h4 style="text-align: center; font-style: italic">
                    {{ $t("There are no records to show") }}
                  </h4>
                </template>
                <template #thead-top="">
                  <b-tr class="saj-title text-center">
                    <!--core-->
                    <b-th class="" variant="primary">
                      <!-- asdad -->
                      {{ $t("Filtered Items") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Need Improvement") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Satisfactory") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Very Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Excellent") }}
                    </b-th>
                  </b-tr>
                </template>
              </b-table>
            </div>
          </b-tab>
          <b-tab :title="$t('HOD Evaluation')">
            <div class="container">
              <div class="d-flex justify-content-end">
                <b-form-input
                  v-model="searchSummary2"
                  style="width: 250px"
                  :placeholder="$t('Search') + '...'"
                  size="sm"
                  class="d-flex align-items-end"
                />
              </div>

              <b-table
                sticky-header="500px"
                responsive
                :items="summaryList2"
                :fields="summaryFields"
                :filter="searchSummary2"
                bordered
                show-empty
                style=""
                class="mt-1 mb-1 saj-subtitle"
              >
                <!-- <template #head()="data">
                <span
                  class="saj-text d-flex justify-content-center"
                >{{ $t(data.label) }}</span>
              </template> -->
                <template #emptyfiltered="">
                  <h4 style="text-align: center; font-style: italic">
                    {{ $t("There are no records to show") }}
                  </h4>
                </template>
                <template #thead-top="">
                  <b-tr class="saj-title text-center">
                    <!--core-->
                    <b-th class="" variant="primary">
                      <!-- asdad -->
                      {{ $t("Filtered Items") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Need Improvement") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Satisfactory") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Very Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Excellent") }}
                    </b-th>
                  </b-tr>
                </template>
                <template #cell(total4)="data">
                  {{ data.item.total4 }}
                  <p
                    v-if="noFilter"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff"
                    @click="selectedGrade = 'Need Improvement'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>
                <template #cell(total3)="data">
                  {{ data.item.total3 }}
                  <p
                    v-if="noFilter"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff"
                    @click="selectedGrade = 'Satisfactory'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>
                <template #cell(total2)="data">
                  {{ data.item.total2 }}
                  <p
                    v-if="noFilter"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff"
                    @click="selectedGrade = 'Good'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>
                <template #cell(total1)="data">
                  {{ data.item.total1 }}
                  <p
                    v-if="noFilter"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff"
                    @click="selectedGrade = 'Very Good'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>
                <template #cell(total)="data">
                  {{ data.item.total }}
                  <p
                    v-if="noFilter"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff"
                    @click="selectedGrade = 'Excellent'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>
              </b-table>
              <b-modal
                id="edit-grade"
                :hide-footer="true"
                :hide-header="true"
                :centered="true"
                size="xl"
              >
                <editGrade
                  :sub-id="userDetails.business_unit"
                  :dept-id="userDetails.department_id"
                  :grade="selectedGrade"
                  @reload="filter()"
                />
              </b-modal>
            </div>
          </b-tab>
        </b-tabs>
      </b-card>
    </b-overlay>
  </div>
</template>

<script>
import {
  BCard,
  BTr,
  BTh,
  BRow,
  // BCol,
  BFormSelect,
  BButton,
  BTable,
  BFormInput,
  // BCardGroup,
  // BPagination,
  BTab,
  BTabs,
  BOverlay,
  BSpinner,
} from "bootstrap-vue";
import { mapGetters } from "vuex";
import Datepicker from "vuejs-datepicker";
import moment from "moment";
import OverallGraph from "@/views/pages/performance-statistics/overall_graph.vue";
import LineGraph from "@/views/pages/performance-statistics/line_graph.vue";
import BarGraph from "@/views/pages/performance-statistics/bar_graph.vue";
import editGrade from "@/views/pages/performance-statistics/edit_grade.vue";

export default {
  components: {
    BCard,
    BRow,
    // BCol,
    BFormSelect,
    BButton,
    BTable,
    Datepicker,
    BFormInput,
    BTr,
    BTh,
    // BCardGroup,
    // BPagination,
    BTab,
    BTabs,
    OverallGraph,
    LineGraph,
    BarGraph,
    BOverlay,
    BSpinner,
    editGrade,
  },

  data() {
    return {
      selectedGrade: null,
      search: false,
      show: false,
      graphValue: null,
      catType: null,
      refreshGraph: 0,
      currentPage: 1,
      currFirstIdx: 0,
      currLastIdx: 6,
      rows: null,
      searchSummary: null,
      searchSummary2: null,
      apex1Counter: 0,
      //   isSelectCompany: false,
      defaultDate: "2022",
      DatePickerFormat: "yyyy",
      filterBy: null,
      tempSummaryField: [
        {
          label: "",
          key: "filtered_items",
        },
        {
          // label: `${this.$i18n.t(`Need Improvement`)}`,
          label: "%",
          key: "perluperingatan",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total4",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          // label: `${this.$i18n.t(`Satisfactory`)}`,
          label: "%",
          key: "memuaskan",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total3",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          // label: `${this.$i18n.t(`Good`)}`,
          label: "%",
          key: "baik",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total2",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          // label: `${this.$i18n.t(`Very Good`)}`,
          label: "%",
          key: "sangatbaik",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total1",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          // label: `${this.$i18n.t(`Excellent`)}`,
          label: "%",
          key: "cemerlang",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total",
          thClass: "text-center",
          tdClass: "text-center",
        },
      ],
      summaryFields: [], // column name === score
      summaryList: [], // filtered item
      summaryList2: [],
      tempListFilter: [
        {
          text: `${this.$i18n.t(`Choose Category`)}`,
          value: null,
          disabled: true,
        },
        {
          text: `${this.$i18n.t(`Department`)}`,
          value: "Department",
        },
        {
          text: `${this.$i18n.t(`Category`)}`,
          value: "Category",
        },
        {
          text: `${this.$i18n.t(`Work Location`)}`,
          value: "Worklocation",
        },
        {
          text: `${this.$i18n.t(`Gender`)}`,
          value: "Gender",
        },
        {
          text: `${this.$i18n.t(`Grade Set`)}`,
          value: "Gradeset",
        },
        {
          text: `${this.$i18n.t(`Grade`)}`,
          value: "Grade",
        },
      ],
      selectedCycle: new Date(),
      cycleList: [],
      selectedDepartment: null,
      departmentList: [],
      selectedCategory: null,
      categoryList: [
        {
          value: 1,
          text: "Executive",
        },
        {
          value: 2,
          text: "Non-Executive",
        },
      ],
      listFilterBy: [],
      selectedCompany: 0,
      initCompanyList: [
        {
          value: 0,
          text: "All",
        },
      ],
      colors: [
        "#3C4EF2",
        "#00ACC1",
        "#FFA346",
        "#9646EF",
        "#999fff",
        "#88bcfc",
        "#00838F",
        "#C4C4C4",
        "#B2EBF2",
      ],
      companyList: [],
      filterByLine: {
        series: [
          {
            data: [],
          },
        ],
        chartOptions: {
          chart: {
            height: 350,
            width: 90,
            type: "line",
            zoom: {
              enabled: false,
            },
          },
          dataLabels: {
            enabled: true,
          },
          stroke: {
            curve: "straight",
          },
          title: {
            text: "",
            align: "left",
          },
          grid: {
            row: {
              colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
              opacity: 0.5,
            },
          },
          xaxis: {
            categories: [],
          },
          yaxis: {
            labels: {
              formatter(val) {
                return `${val.toFixed(0)} %`;
              },
            },
            min: 0,
            max: 100,
            tickAmount: 10,
          },
          tooltip: {
            // theme: 'dark',
            shared: false,
            enabled: true,
            x: {
              show: false,
            },
            y: {
              formatter(val) {
                return val.toFixed(0);
              },
            },
            // fillSeriesColor: true,
          },
          legend: {
            show: false,
          },
        },
      },
      // Sini Bar Graph
      filterByBar: {
        series: [
          {
            name: "Net Profit",
            data: [44, 55, 57, 56, 61, 58, 63, 60, 66],
          },
        ],
        chartOptions: {
          title: {
            text: "Product Trends by Month",
            align: "left",
          },
          chart: {
            type: "bar",
            height: 350,
            width: 90,
            // zoom: {
            //   enabled: true,
            //   type: 'xy',
            //   resetIcon: {
            //     offsetX: -10,
            //     offsetY: 0,
            //     fillColor: '#fff',
            //     strokeColor: '#37474F',
            //   },
            //   selection: {
            //     background: '#90CAF9',
            //     border: '#0D47A1',
            //   },
            // },
          },
          plotOptions: {
            bar: {
              columnWidth: "100%",
              distributed: true,
            },
          },
          colors: [
            "#008FFB",
            "#00E396",
            "#FEB019",
            "#FF4560",
            "#775DD0",
            "#3F51B5",
            "#546E7A",
            "#D4526E",
            "#8D5B4C",
            "#F86624",
            "#D7263D",
            "#1B998B",
            "#2E294E",
            "#F46036",
            "#E2C044",
          ],
          dataLabels: {
            enabled: false,
          },
          stroke: {
            show: true,
            width: 2,
            colors: ["transparent"],
          },
          xaxis: {
            categories: [
              "Feb",
              "Mar",
              "Apr",
              "May",
              "Jun",
              "Jul",
              "Aug",
              "Sep",
              "Oct",
            ],
            scrollbar: {
              enabled: true,
            },
            type: "category",
            // labels: {
            //   formatter(val) {
            //     console.log('val', this.$t(val))
            //     // return `${this.$i18n.t(val)}`
            //   },
            // },
          },
          fill: {
            opacity: 1,
          },
          yaxis: {
            labels: {
              // formatter(value) {
              //   let val = Math.abs(value)
              //   if (val >= 1000) {
              //     val = `${(val / 1000).toFixed(2)}`
              //   }
              //   return val
              // },
              formatter(val) {
                // console.log('val', val)
                return `${val.toFixed(0)} %`;
              },
              scrollbar: {
                enabled: true,
              },
            },
            min: 0,
            max: 100,
            tickAmount: 10,
          },
          tooltip: {
            // theme: 'dark',
            shared: false,
            enabled: true,
            x: {
              show: false,
            },
            y: {
              formatter(val) {
                return val.toFixed(0);
              },
            },
            // fillSeriesColor: true,
          },
          legend: {
            show: false,
          },
        },
      },
      graphData: [],
      isOverall: false,
      noFilter: false,
    };
  },
  computed: {
    ...mapGetters(["roles", "userDetails"]),
  },
  watch: {
    // eslint-disable-next-line func-names
    "$i18n.locale": function () {
      // this.getCompanyList()
      this.checkRoleFilter();
      this.tempSummaryField = [
        {
          label: "",
          key: "filtered_items",
        },
        {
          // label: `${this.$i18n.t(`Need Improvement`)}`,
          label: "%",
          key: "perluperingatan",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total4",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          // label: `${this.$i18n.t(`Satisfactory`)}`,
          label: "%",
          key: "memuaskan",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total3",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          // label: `${this.$i18n.t(`Good`)}`,
          label: "%",
          key: "baik",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total2",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          // label: `${this.$i18n.t(`Very Good`)}`,
          label: "%",
          key: "sangatbaik",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total1",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          // label: `${this.$i18n.t(`Excellent`)}`,
          label: "%",
          key: "cemerlang",
          thClass: "text-center",
          tdClass: "text-center",
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: "total",
          thClass: "text-center",
          tdClass: "text-center",
        },
      ];
      this.tempListFilter = [
        {
          text: `${this.$i18n.t(`Choose Category`)}`,
          value: null,
          disabled: true,
        },
        {
          text: `${this.$i18n.t(`Department`)}`,
          value: "Department",
        },
        {
          text: `${this.$i18n.t(`Category`)}`,
          value: "Category",
        },
        {
          text: `${this.$i18n.t(`Work Location`)}`,
          value: "Worklocation",
        },
        {
          text: `${this.$i18n.t(`Gender`)}`,
          value: "Gender",
        },
        {
          text: `${this.$i18n.t(`Grade Set`)}`,
          value: "Gradeset",
        },
        {
          text: `${this.$i18n.t(`Grade`)}`,
          value: "Grade",
        },
      ];
      this.listFilterBy = this.tempListFilter;
      this.filter();
    },
  },
  mounted() {
    this.getCompanyList();
    // this.checkRoleFilter()
    this.listFilterBy = this.tempListFilter;

    // this.filter()
    this.selectedCompany = this.userDetails.business_unit;
    // console.log(this.userDetails)
  },
  methods: {
    range(start, end) {
      // const arr = [1, 2, 3, 4, 5].map((_, idx) => start + idx)
      // console.log('arr', arr)
      // console.log(end, start)
      // console.log('array 5', Array(end - start))
      return Array(end - start !== 0 ? end - start : 1)
        .fill()
        .map((_, idx) => start + idx);
    },
    clearButton() {
      this.filterBy = null;
      this.selectedCycle = new Date();
      this.graphData = [];
      this.refreshGraph += 1;
      this.isOverall = true;
      // this.filter()
      this.show = false;
      this.search = false;
