        if (this.filterBy === null) {
          x = "department";
          this.noFilter = true;
        } else {
          x = this.filterBy.toLowerCase();
          this.noFilter = false;
        }

        this.graphValue =
          this.filterBy === null ? datas.department : datas.graph;
        this.catType =
          this.filterBy === null ? "department" : this.filterBy.toLowerCase();
        // this.show = false
        this.search = false;
        this.refreshGraph = 0;
        if (this.filterBy === null) {
