<template>
  <div>
    <b-card
      no-body
      class=""
    >
      <div class="">
        <b-row class="p-1">
          <!-- v-if="roles.isAdmin" -->
          <div
            v-if="roles.isAdmin"
            class="d-flex flex-column ml-1">
            <!-- {{ companyList }} -->
            <!-- {{ selectedCompany }} -->
            <label class="saj-text">{{ $t("Company") }}:</label>
            <b-form-select
              v-model="selectedCompany"
              :options="companyList"
              size="sm"
              class="saj-text m-0"
              style="width: 190px; margin-left: 14px; height: 34px"
            />
          </div>
          <div class="d-flex flex-column ml-1">
            <label
              class="saj-text"
              style="margin-left: 1px;"
            >{{ $t("Cycle") }}:</label>
            <Datepicker
              id="input-id"
              v-model="selectedCycle"
              :value="customFormatter"
              :format="customFormatter"
              minimum-view="year"
              name="datepicker"
              input-class="year-custom"
              class="show-only-year"
            />
            <!-- </div> -->
          </div>
          <div
            v-if="selectedCompany !== 0 || !roles.isAdmin"
            class="d-flex flex-column ml-1"
          >
            <label class="saj-text">{{ $t("Filter by") }}:</label>
            <div class="row">
              <b-form-select
                v-model="filterBy"
                :options="listFilterBy"
                size="sm"
                class="saj-text"
                style="width: 190px; margin-left: 14px; height: 34px"
              />
            </div>

          </div>
        </b-row>
        <b-row class="p-1 d-flex justify-content-end align-items-end">
          <b-button
            md="4"
            variant="primary"
            class="saj-button mr-1"
            block
            style="width: 120px; height: 45px;"
            @click="filter()"
          >
            <!-- <feather-icon icon="SearchIcon" /> -->
            <span
              class=""
              style=""
            > {{ $t("Filter") }} </span>
          </b-button>
          <b-button
            md="4"
            variant="primary"
            class="mr-1 saj-button"
            block
            style="width: 120px; height: 45px;"
            :disabled="selectedCompany === 0"
            @click="clearButton()"
          >
            <span
              class=""
              style=""
            > {{ $t("Clear") }} </span>
          </b-button>
        </b-row>
      </div>
      <!-- {{ graphData }} -->
    </b-card>
    <b-overlay
      :show="search"
      rounded="sm"
      class="mt-3"
    >
      <template #overlay>
        <div
          class="d-flex flex-column align-items-center justify-content-center"
          style="margin-top: 30vh;"
        >
          <b-spinner
            variant="primary"
            label="Spinning"
          />
          <p class="mt-1">
            {{ $t('Fetching Data') }}...
          </p>
        </div>

      </template>
      <div v-if="show">
        <div
          v-if="isOverall && roles.isAdmin"
          :key="refreshGraph"
          class=""
        >
          <overall-graph
            :graph="graphValue"
            :category-type="catType"
          />
        </div>
        <div
          v-else
          :key="refreshGraph"
          class="m-0"
        >
          <b-tabs content-class="mt-1">
            <b-tab
              :title="$t('Bar Graph')"
              active
            >
              <bar-graph
                :graph="graphValue"
                :category-type="catType"
              />
            </b-tab>
            <b-tab :title="$t('Line Graph')">
              <line-graph
                :graph="graphValue"
                :category-type="catType"
              />
            </b-tab>
            <b-tab
              v-if="catType !== 'performance'"
              :title="$t('Overall Graph')"
            >
              <overall-graph
                :graph="graphValue"
                :category-type="catType"
              />
            </b-tab>
          </b-tabs>

        </div>
      </div>

      <b-card v-if="show">
        <!-- summary table -->
        <span class="saj-title">{{ $t("Summary Table") }}</span>
        <b-tabs content-class="mt-1">
          <b-tab
            :title="$t('Performance Grade')"
            active
          >
            <div class="container">
              <div class="d-flex justify-content-end">
                <b-form-input
                  v-model="searchSummary"
                  style="width: 250px;"
                  :placeholder="$t('Search') + '...'"
                  size="sm"
                  class="d-flex align-items-end"
                />
              </div>

              <b-table
                sticky-header="500px"
                responsive
                :items="summaryList"
                :fields="summaryFields"
                :filter="searchSummary"
                bordered
                show-empty
                style=""
                class="mt-1 mb-1 saj-subtitle"
              >
                <!-- <template #head()="data">
                <span
                  class="saj-text d-flex justify-content-center"
                >{{ $t(data.label) }}</span>
              </template> -->
                <template #emptyfiltered="">
                  <h4 style="text-align: center; font-style: italic">
                    {{ $t('There are no records to show') }}
                  </h4>
                </template>
                <template #thead-top="">
                  <b-tr class="saj-title text-center">
                    <!--core-->
                    <b-th
                      class=""
                      variant="primary"
                    >
                      <!-- asdad -->
                      {{ $t('Filtered Items') }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Need Improvement") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Satisfactory") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Very Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Excellent") }}
                    </b-th>
                  </b-tr>
                </template>

              </b-table>
            </div>
          </b-tab>
          <b-tab
            :title="$t('HOD Evaluation')"
          >
            <div class="container">
              <div class="d-flex justify-content-end">
                <b-form-input
                  v-model="searchSummary1"
                  style="width: 250px;"
                  :placeholder="$t('Search') + '...'"
                  size="sm"
                  class="d-flex align-items-end"
                />
              </div>

              <b-table
                sticky-header="500px"
                responsive
                :items="summaryList1"
                :fields="summaryFields"
                :filter="searchSummary1"
                bordered
                show-empty
                style=""
                class="mt-1 mb-1 saj-subtitle"
              >
                <!-- <template #head()="data">
                <span
                  class="saj-text d-flex justify-content-center"
                >{{ $t(data.label) }}</span>
              </template> -->
                <template #emptyfiltered="">
                  <h4 style="text-align: center; font-style: italic">
                    {{ $t('There are no records to show') }}
                  </h4>
                </template>
                <template #thead-top="">
                  <b-tr class="saj-title text-center">
                    <!--core-->
                    <b-th
                      class=""
                      variant="primary"
                    >
                      <!-- asdad -->
                      {{ $t('Filtered Items') }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Need Improvement") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Satisfactory") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Very Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Excellent") }}
                    </b-th>
                  </b-tr>
                </template>

              </b-table>
            </div>
          </b-tab>
          <b-tab
            :title="$t('Committee Evaluation')"
          >
            <div class="container">
              <div class="d-flex justify-content-end">
                <b-form-input
                  v-model="searchSummary2"
                  style="width: 250px;"
                  :placeholder="$t('Search') + '...'"
                  size="sm"
                  class="d-flex align-items-end"
                />
              </div>

              <b-table
                sticky-header="500px"
                responsive
                :items="summaryList2"
                :fields="summaryFields"
                :filter="searchSummary2"
                bordered
                show-empty
                style=""
                class="mt-1 mb-1 saj-subtitle"
              >
                <!-- <template #head()="data">
                <span
                  class="saj-text d-flex justify-content-center"
                >{{ $t(data.label) }}</span>
              </template> -->
                <template #emptyfiltered="">
                  <h4 style="text-align: center; font-style: italic">
                    {{ $t('There are no records to show') }}
                  </h4>
                </template>
                <template #thead-top="">
                  <b-tr class="saj-title text-center">
                    <!--core-->
                    <b-th
                      class=""
                      variant="primary"
                    >
                      <!-- asdad -->
                      {{ $t('Filtered Items') }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Need Improvement") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Satisfactory") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Very Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Excellent") }}
                    </b-th>
                  </b-tr>
                </template>
                <template #cell(total4)="data">
                  {{ data.item.total4 }}
                  <p
                    v-if="isAll"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff;"
                    @click="selectedGrade = 'Need Improvement'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>
                <template #cell(total3)="data">
                  {{ data.item.total3 }}
                  <p
                    v-if="isAll"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff;"
                    @click="selectedGrade = 'Satisfactory'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>
                <template #cell(total2)="data">
                  {{ data.item.total2 }}
                  <p
                    v-if="isAll"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff;"
                    @click="selectedGrade = 'Good'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>
                <template #cell(total1)="data">
                  {{ data.item.total1 }}
                  <p
                    v-if="isAll"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff;"
                    @click="selectedGrade = 'Very Good'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>
                <template #cell(total)="data">
                  {{ data.item.total }}
                  <p
                    v-if="isAll"
                    v-b-modal.edit-grade
                    class="mt-1"
                    style="text-decoration: underline; color: #5555ff;"
                    @click="selectedGrade = 'Excellent'"
                  >
                    {{ $t("Edit Grade") }}
                  </p>
                </template>

              </b-table>
              <b-modal
                id="edit-grade"
                :hide-footer="true"
                :hide-header="true"
                :centered="true"
                size="xl"
              >
                <editGrade
                  :sub-id="selectedCompany"
                  :grade="selectedGrade"
                  @reload="filter()"
                />
              </b-modal>
            </div>
          </b-tab>
        </b-tabs>

      </b-card>
    </b-overlay>
  </div>
</template>

<script>
import {
  BCard,
  BTr,
  BTh,
  BRow,
  // BCol,
  BFormSelect,
  BButton,
  BTable,
  BFormInput,
  // BCardGroup,
  // BPagination,
  BTab,
  BTabs,
  BOverlay,
  BSpinner,
} from "bootstrap-vue"
import { mapGetters } from 'vuex'
import Datepicker from 'vuejs-datepicker'
import moment from "moment"
import OverallGraph from '@/views/pages/performance-statistics/overall_graph.vue'
import LineGraph from '@/views/pages/performance-statistics/line_graph.vue'
import BarGraph from '@/views/pages/performance-statistics/bar_graph.vue'
import editGrade from "@/views/pages/performance-statistics/edit_grade.vue"
// import statisticsHod from '@/views/pages/performance-statistics/performance-statistics_hod.vue'

export default {
  components: {
    BCard,
    BRow,
    // BCol,
    BFormSelect,
    BButton,
    BTable,
    Datepicker,
    BFormInput,
    BTr,
    BTh,
    // BCardGroup,
    // BPagination,
    BTab,
    BTabs,
    OverallGraph,
    LineGraph,
    BarGraph,
    BOverlay,
    BSpinner,
    editGrade,
    // statisticsHod,
  },

  data() {
    return {
      search: false,
      show: false,
      graphValue: null,
      catType: null,
      refreshGraph: 0,
      currentPage: 1,
      currFirstIdx: 0,
      currLastIdx: 6,
      rows: null,
      searchSummary: null,
      searchSummary1: null,
      searchSummary2: null,
      apex1Counter: 0,
      //   isSelectCompany: false,
      defaultDate: '2022',
      DatePickerFormat: 'yyyy',
      filterBy: null,
      tempSummaryField: [
        {
          label: '',
          key: 'filtered_items',
        },
        {
          // label: `${this.$i18n.t(`Need Improvement`)}`,
          label: '%',
          key: 'perluperingatan',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total4',
        },
        {
          // label: `${this.$i18n.t(`Satisfactory`)}`,
          label: '%',
          key: 'memuaskan',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total3',
        },
        {
          // label: `${this.$i18n.t(`Good`)}`,
          label: '%',
          key: 'baik',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total2',
        },
        {
          // label: `${this.$i18n.t(`Very Good`)}`,
          label: '%',
          key: 'sangatbaik',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total1',
        },
        {
          // label: `${this.$i18n.t(`Excellent`)}`,
          label: '%',
          key: 'cemerlang',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total',
        },
      ],
      summaryFields: [], // column name === score
      summaryList: [], // filtered item
      summaryList1: [],
      summaryList2: [],
      tempListFilter: [
        {
          text: `${this.$i18n.t(`Choose Category`)}`,
          value: null,
          // disabled: true,
        },
        {
          text: `${this.$i18n.t(`Department`)}`,
          value: 'Department',
        },
        {
          text: `${this.$i18n.t(`Category`)}`,
          value: 'Category',
        },
        {
          text: `${this.$i18n.t(`Work Location`)}`,
          value: 'Worklocation',
        },
        {
          text: `${this.$i18n.t(`Gender`)}`,
          value: 'Gender',
        },
        {
          text: `${this.$i18n.t(`Grade Set`)}`,
          value: 'Gradeset',
        },
        {
          text: `${this.$i18n.t(`Grade`)}`,
          value: 'Grade',
        },
      ],
      selectedCycle: new Date(),
      cycleList: [],
      selectedDepartment: null,
      departmentList: [],
      selectedCategory: null,
      categoryList: [{
        value: 1,
        text: 'Executive',
      }, {
        value: 2,
        text: 'Non-Executive',
      }],
      listFilterBy: [],
      selectedCompany: 0,
      initCompanyList: [{
        value: 0,
        text: 'All',
      }],
      colors: ['#3C4EF2', '#00ACC1', '#FFA346', '#9646EF', "#999fff", "#88bcfc", "#00838F", "#C4C4C4", "#B2EBF2"],
      companyList: [],
      graphData: [],
      isOverall: false,
      isAll: false,
      selectedGrade: null,
    }
  },
  computed: {
    ...mapGetters(['roles', 'userDetails']),
  },
  watch: {
    // eslint-disable-next-line func-names
    '$i18n.locale': function () {
      // this.getCompanyList()
      this.checkRoleFilter()
      this.tempSummaryField = [
        {
          label: '',
          key: 'filtered_items',
        },
        {
          // label: `${this.$i18n.t(`Need Improvement`)}`,
          label: '%',
          key: 'perluperingatan',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total4',
        },
        {
          // label: `${this.$i18n.t(`Satisfactory`)}`,
          label: '%',
          key: 'memuaskan',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total3',
        },
        {
          // label: `${this.$i18n.t(`Good`)}`,
          label: '%',
          key: 'baik',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total2',
        },
        {
          // label: `${this.$i18n.t(`Very Good`)}`,
          label: '%',
          key: 'sangatbaik',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total1',
        },
        {
          // label: `${this.$i18n.t(`Excellent`)}`,
          label: '%',
          key: 'cemerlang',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total',
        },
      ]
      this.tempListFilter = [
        {
          text: `${this.$i18n.t(`Choose Category`)}`,
          value: null,
          // disabled: true,
        },
        {
          text: `${this.$i18n.t(`Department`)}`,
          value: 'Department',
        },
        {
          text: `${this.$i18n.t(`Category`)}`,
          value: 'Category',
        },
        {
          text: `${this.$i18n.t(`Work Location`)}`,
          value: 'Worklocation',
        },
        {
          text: `${this.$i18n.t(`Gender`)}`,
          value: 'Gender',
        },
        {
          text: `${this.$i18n.t(`Grade Set`)}`,
          value: 'Gradeset',
        },
        {
          text: `${this.$i18n.t(`Grade`)}`,
          value: 'Grade',
        },
      ]
      this.listFilterBy = this.tempListFilter
      this.filter()
    },
  },
  mounted() {
    this.getCompanyList()
    // this.checkRoleFilter()
    this.listFilterBy = this.tempListFilter

    this.filter()
  },
  methods: {
    range(start, end) {
      // const arr = [1, 2, 3, 4, 5].map((_, idx) => start + idx)
      // console.log('arr', arr)
      // console.log(end, start)
      // console.log('array 5', Array(end - start))
      return Array(end - start !== 0 ? (end - start) : 1).fill().map((_, idx) => start + idx)
    },
    clearButton() {
      this.selectedCompany = 0
      this.filterBy = null
      this.selectedCycle = new Date()
      // console.log(this.filterBy)
      this.filter()
      this.isOverall = true
      this.show = false
      this.search = false
      this.refreshGraph += 1
      this.isAll = false

      // this.filter()
      this.graphData = []
    },
    customFormatter(date) {
      this.scoreYearSelected = moment(date).format('YYYY')
      return this.scoreYearSelected
    },
    getCompanyList() { // get all company
      const config = {
        method: 'get',
        url: `${this.$baseUrl}/subsidiary/getAll`,
      }

      this.$axios(config)
        .then(response => {
          // console.log(response.data.data)
          const lists = response.data.data.subsidiaries
          const filteredList = lists.map(comp => ({
            value: comp.id,
            text: comp.subsidiary_name,
          }))
          this.companyList = this.initCompanyList.concat(filteredList)
        })
        .catch(() => {
          // console.log(error)
        })
    },
    checkRoleFilter() { // check filter options for each role
      // if (this.roles.isHOD) {
      //   console.log('masuk')
      //   this.listFilterBy = this.tempListFilter.filter(x => x !== 'Department')
      // } else {
      //   this.listFilterBy = this.tempListFilter
      // }
      this.listFilterBy = this.tempListFilter
    },
    async filter() {
      this.graphData = []
      this.refreshGraph += 1
      this.show = false
      this.selectedCompany !== 0 ? this.isOverall = false : this.isOverall = true
      this.search = true
      this.currentPage = 1
      const data = new FormData()
      data.append('year', moment(this.selectedCycle).year())

      data.append('company', this.selectedCompany === 0 ? 'all' : this.selectedCompany)

      if (this.filterBy !== null) {
        data.append('filter', this.filterBy.toLowerCase())
      }

      data.append('status', 'active')

      try {
        const response = await this.$axios.post(`${this.$baseUrl}/dashboard/graph`, data)

        const datas = response.data.data
        
        let x = null

        if (this.selectedCompany !== 0){
          if (this.filterBy === null){
            x = 'performance'
            this.isAll = true
          } else {
            x = this.filterBy.toLowerCase()
            this.isAll = false
          }
        } else {
          x = ''
        }

        this.graphValue = this.filterBy === null && this.selectedCompany !== 0 ? datas.performance_grade : datas.graph
        this.catType = x
        // this.show = false
        this.search = false
        this.refreshGraph = 0
        if (this.selectedCompany === 0){
          this.isOverall = true
        }

        this.changeToGraph(x, this.graphValue)
      } catch (error) {
        console.error('ini err', error)
      }
    },
    changeToGraph(type, data) {
      let summary = []
      console.log('change', data.length, type)
      if (type === 'performance'){
        summary = data.map(x => x.summary !== 'company_name')
      } else {
        summary = data.map(x => x.summary)
      }

      if (summary.length !== 0) {
        // this.summaryFields = this.tempSummaryField.concat(Object.keys(summary[0]).filter(x => x !== 'total'))
        this.summaryFields = this.tempSummaryField.concat(Object.keys(summary[0]))
      } else {
        this.summaryFields = []
      }

      let filterGenderTable = []

      console.log('summ fields', this.summaryFields)
      // console.log('type', type, data)

